.about-container {
    padding: 1rem; /* Adjust padding as per theme */
  }
  
  .page-title {
    font-size: 2rem;
    color: #1E0D31; /* Adjust color as per theme */
    margin-bottom: 20px;
  }
  
  .section-title {
    font-size: 1.5rem;
    font-weight: 700;
    margin: 1rem 0; /* Adjust margin */
  }
  
  .learn-section,
  .toolkit-section {
    margin: 2rem 0; /* Add spacing above and below the section */
  }
  
  .learn-link {
    display: block;
    color: #17a2b8; /* Different color for links */
    text-decoration: underline;
    margin-bottom: 0.5rem;
  }
  
  .learn-link:hover {
    text-decoration: none;
    color: #FFCF77; /* Optional: change color on hover */
  }
  
  .list {
    list-style: none;
    padding: 0;
    margin-left: 20px;
  }
  
  .list li {
    position: relative;
    padding-left: 1.5rem; /* Adjust space for arrow */
    margin-bottom: 0.5rem; /* Adjust margin between list items */
  }
  
  .list li::before {
    content: 'âžœ';
    position: absolute;
    left: 0;
    color: #1E0D31; /* Adjust arrow color if needed */
  }
  